.action {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	padding-left: 20px;
}

.action.inactive {
	color: gray;
}

.action:hover {
	background: rgba(0,0,0,0.1);
	cursor: pointer;
}

.action.inactive:hover {
	background: white;
	cursor: default;
}


